import React, { Component } from 'react'
import axios from "../../../../server/node_modules/axios";
import { Redirect } from 'react-router-dom';
import { Card, Row, Col, Container } from "react-bootstrap";
import "./cart.css"




// let myKey = "d759a614b16c0c1c0295b4313e94aeec";
let baseImgUrl = "https://image.tmdb.org/t/p/w500";
class Cart extends Component {

    state = { isError: false, RedirectToCart: false, MyCart: [], user: JSON.parse(sessionStorage.getItem("user") || null)};

    cart = () => {

        axios.post('/cart:id', {
            MyCart: this.state.MyCart
        }).then(res => {

            if (res.status === 201) {
                this.setState({ RedirectToCart: true });
                this.props.setUser({ MyCart: this.state.MyCart });
            }
            else {
                this.setState({ isError: true })
                console.log(`error code : ${res.status}`);

            }

        }).catch(err => {
            this.setState({ isError: true })
            console.log(err);
        })
    }

    removeFromCart(movieToRemove) {
        const myCart = [...this.state.MyCart];
       const index =  myCart.findIndex(movie =>{  
            return (movie.id== movieToRemove.id);
        })
        //find movie index
        //remove movie by index from array
        
        myCart.splice(index,1);
        this.setState({MyCart:myCart});

         
    }

        componentDidMount(){
            
        axios.get(`/user/${this.state.user._id}`).then(res =>{

        if(res.status === 200){
            
            let user = res.data;
            console.log(user.MyCart);
            this.setState({MyCart: user.MyCart});
        }

        }).catch(err => {
            
            console.log(err)
        })          
    }
         
    deleteMovie = movieObj => {
        console.log(movieObj, this.state.user._id);
        axios
          .delete(`/cart/${this.state.user._id}/${movieObj.id}`)
          .then(res => {

            this.removeFromCart(movieObj);
            console.log(res.data);
          })
          .catch(err => {
            console.log(err);
          });
      };
    
    
    
    
    
    render() {
        
        const userList = this.state.MyCart.map((movie,index) => (
            <li key={index}>
            <div class="image-container">
            <img src={`${baseImgUrl}/${movie.poster_path}`}/>
            <span class="score-buble"> {movie.vote_average}</span>
            </div>
          <span class="title"> {movie.title ? movie.title : movie.name}</span>
           {/* <p> {movie.overview}</p> */}
          
           <button onClick={() => this.deleteMovie(movie)}>Delete</button>
            </li>
        ));
        

        if (this.state.RedirectToCart) {
            return <Redirect to='/cart' />
        }
        return (
            <Container className="cartPage">
                <h1>Cart page</h1>
                
                    
                <ul>
                    {userList}
                </ul>

            </Container>
        )
    }
}
export default Cart;