import React, { Component } from "react";
import axios from "axios";
import SearchArea from "../mySearchArea/mySearchArea";
import MovieList from "../Movie List/movie_list_Results";
import "./home.css";
import { Card, Row, Col, Container } from "react-bootstrap";
import { Redirect } from "react-router-dom";

import Slider from "react-slick";
// import "~slick-carousel/slick/slick.css"; 
// import "~slick-carousel/slick/slick-theme.css";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

const baseImgUrl = "https://image.tmdb.org/t/p/w500";
let myKey = "d759a614b16c0c1c0295b4313e94aeec";

class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      moviesTop: [], // home-movies
      movies: [], // home-search-movies
      isLoaded: false,
      moviesPopular: [],
      tvShowsTop: [],
      searchTerm: "",//to search Helper
      tvShowsPopular: [],
      totalResuolts: 0,
      currentPage: 1,
      movie: null,
      flag: false,
      flag2 : false,
      user: JSON.parse(sessionStorage.getItem("user") || null)
    };
    console.log("constructor is working");
  }
  // __________movie and TVshow____________
  async componentDidMount() {
    const response = await axios.get("http://localhost:8080/home");
    const moviesData = response.data.movies;
    console.log(moviesData);
    console.log("componentDidMount is working");
    this.setState({ moviesTop: moviesData });

    const res = await axios.get("http://localhost:8080/home/tv");
    const tvshowData = res.data.tvshow;
    console.log(tvshowData);
    console.log("componentDidMount2 is working");
    this.setState({ tvShowsPopular: tvshowData });



  }

  // _______________________
  
  //  nextPage = (pageNumber)=> {
  //   axios
  //   .get(
  //     `https://api.themoviedb.org/3/movie/upcoming?api_key=${myKey}&query=&language=en-US&page=${pageNumber}`
  //   )
  //   .then(res => {
  //     console.log(res.data.results);
    
  //   })
  //   .catch(err => {
  //     console.log(err);
  //   });
  // console.log("next page is done");

  // }

  // search movie
  handleSubmit = e => {
    e.preventDefault();
    axios
      .get(
        `https://api.themoviedb.org/3/search/multi?api_key=${myKey}&query=${this.state.searchTerm}`
      )
      .then(res => {
        console.log(res.data.results);
        this.setState({
          movies: [...res.data.results],
          totalResuolts: res.data.total_results
        });
      })
      .catch(err => {
        console.log(err);
      });
    console.log("search is done");
  };
  //searchTerm change value function
  handleChange = e => {
    this.setState({ searchTerm: e.target.value });
  };
  // _______________________
  movieInfo = movie => {
    this.setState({
      flag: true,
      movie: movie
    });
  };
  // _______________________

  addItemToList = movieObj => {
    
    // console.log(movieObj, this.state.user._id);
    if(sessionStorage.length > 0 ){
   console.log("inside post")
      axios
      .post(`/cart/${this.state.user._id}`, {
        movie: movieObj
      })
      .then(res => {
        console.log(res);
      })
      .catch(err => {
        console.log(err);
      });
      
    }
    else{
      console.log("aaa");
      return (
        <Redirect
          to={{
            pathname: "/About",
            
          }}
        />
      );
    }
  };

  

  
  render() {
    if (this.state.flag) {
      return (
        <Redirect
          to={{
            pathname: "/ShowPage",
            movie: this.state.movie
          }}
        />
      );
    }
    // _______________________

    const homeMovies = this.state.moviesTop.map((movie, index) => (
      <Col md="3" >
        <Card className="movie-card">
          <Card.Img
            onClick={() => this.movieInfo(movie)}
            variant="top "
            src={`${baseImgUrl}/${movie.poster_path}`}
            // alt={"a"}
          />

          <Card.Body>
            <Card.Title className="card-title" key={index}>
              {movie.title}
            </Card.Title>
            {/* <Card.Text className="card-text">{movie.overview}</Card.Text> */}
            {/* <Card.Text className="card-text">{movie.vote_average}</Card.Text> */}
            <button onClick={() => this.addItemToList(movie)}>Add</button>
          </Card.Body>
        </Card>
      </Col>
    ));

    
    // ______________________
    const homeTvshow = this.state.tvShowsPopular.map((movie, index) => (
      <Col md="3">
        <Card className="movie-card">
          <Card.Img
            onClick={() => this.movieInfo(movie)}
            variant="top "
            src={`${baseImgUrl}/${movie.poster_path}`}
            // alt={"a"}
          />
          <Card.Body>
            <Card.Title className="card-title" key={index}>
              {movie.name}
            </Card.Title>
            {/* <Card.Text className="card-text">{movie.overview}</Card.Text> */}
            {/* <Card.Text className="card-text">{movie.vote_average}</Card.Text> */}
            <button onClick={() => this.addItemToList(movie)}>Add</button>
          </Card.Body>
        </Card>
      </Col>
    ));
    // ______________________
    
    const settings = {
     // dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 5,
      slidesToScroll: 3,
      arrows:true
    };

    
    const carusselMovies = this.state.moviesTop.map((movie, index) => (
      <div className="carussel-card">
        <img  src={`${baseImgUrl}/${movie.poster_path}`} />
        <div>{movie.title}</div>
        <button onClick={() => this.addItemToList(movie)}>Add</button>
      </div>
     
      
    ));

    //------------------------------------------------------------
    return (
      <div className="Home ">
        <h1 id="header-title">Welcome To Fox Movie</h1>


        <Container>
          <SearchArea
            handleSubmit={this.handleSubmit}
            handleChange={this.handleChange}
          />
        </Container>


        <Container>
        <Slider   {...settings}>
        {carusselMovies}
          </Slider>
      </Container>

        <Container>
          <Row>
            <MovieList movies={this.state.movies} />
          </Row>
        </Container>

        <div className="allMovies">
          <Container>
            <Row>
            <div class="col-md-12 gener-title"> Movies </div>
              {!this.state.totalResuolts ? homeMovies : ""}
              <div class="col-md-12 gener-title"> TV series </div>
              {!this.state.totalResuolts ? homeTvshow : ""}
            </Row>
            <button onClick={this.nextPage}> Next</button>
          </Container>
        </div>
      </div>
    );
  }
}

export default Home;
